/* reset css */
@import-normalize;

/* reset default weight of heading ... */
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: unset;
}

/* import montserrat font */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

/* global styling file */
body {
    margin: 0;
    color: #FF6060;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    line-height: 142.6%;
}

* {
    box-sizing: border-box;
}